/***
  vds-search
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

[type='search'] {
  -moz-appearance: none;
  -webkit-appearance: none;

  &::-ms-clear,
  &::-ms-reveal {
    display: none;
    height: 0;
    width: 0;
  }

  // NOTE: combining -ms and -webkit pseudo element overrides will not work in IE11
  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.vds-search {
  position: relative;

  .vds-input {
    margin-top: 0;
    min-width: 18rem; // 288px
    width: 100%;

    &,
    &.vds-state--filled {
      .vds-input-bar {
        width: 100%;
      }
    }
  }

  .vds-input--input-text {
    box-sizing: border-box;
    font-size: 1rem;
    height: 3rem; // 48px
    padding: 0 3.375rem 0 .5rem; // 54px
    width: 100%;
  }

  .vds-input-label {
    display: none;
  }

  .vds-search-button {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
  }

  &--dynamic {
    z-index: $utils-layer-4;

    .vds-input--input-text {
      height: 2.5rem; // 40px
      padding: 0 2.35rem;
    }

    .vds-search-icon {
      height: 1.5rem; // 24px
      left: .5rem;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 1.5rem;

      [class^='vds-icon'] {
        @include svg-icon(1.5rem, $color-indigo);
      }
    }

    .vds-search-clear-btn {
      display: none;
      height: 2rem;
      position: absolute;
      right: .25rem;
      top: 50%;
      transform: translateY(-50%);
      width: 2rem;
    }

    .vds-menu--absolute {
      width: 100%;
    }

    .vds-menu-curtain {
      z-index: -1;
    }
  }

  &--expandable {

    &.vds-search--container {
      min-height: 48px;
    }

    .vds-input {
      margin-left: auto;
      margin-right: 1.5rem;
      max-width: 0;
      min-width: 0;
      overflow: hidden;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .25s ease;
    }

    .vds-input--input-text {
      padding-right: 3.375rem; // 54px
    }

    .vds-state--expanded {
      margin-right: 0;
      max-width: 100%;
      min-width: 18rem;

      .vds-input-bar {
        width: calc(100% - 2px);
      }
    }
  }

  // for vds-nav-bar inclusion
  &--nav-bar {
    .vds-search-button {
      position: relative;
    }

    .vds-input {
      &,
      &:hover,
      &.vds-state--focus,
      &.vds-state--filled {
        .vds-input-bar {
          border: 0;

          &::before {
            border: 0;
          }
        }
      }
    }
  }
}
